import React, {Component} from 'react';
import {connect} from 'react-redux';

import ModalBaseContainer from './ModalBaseContainer';
import AddCollectionModalContainer from './AddCollectionModalContainer';

import {simulateClick} from '../Helpers'
import {loadCollections, unloadCollections, loadAdditionalCollections} from '../actions/collectionsActions';
import {loadEffects, unloadEffects, loadAdditionalEffects} from '../actions/effectsActions';
import {loadSounds, unloadSounds, loadAdditionalSounds} from '../actions/soundsActions';
import {loadApp,changeAppVar} from '../actions/appActions';

import {loadData, exportData} from '../js/file_manager_new'

class ControlsContainer extends Component{
  constructor(props){
    super(props);

    this.load = this.load.bind(this);
    this.add = this.add.bind(this);
    this.export = this.export.bind(this);
    this.closeAddModal = this.closeAddModal.bind(this);
    this.confirmAddModal = this.confirmAddModal.bind(this);
    this.removeAddedCollection = this.removeAddedCollection.bind(this);

    this.state = {modalData:null, showModal:false, addData: null};
  }

  load(e){
    loadData(e,{
      onLoad: () =>{
        this.props.changeAppVar('loading',true);
      },
      onFailure: ()=>{
        this.props.changeAppVar('loading',false);
      },
      onSuccess: (state) =>{
        this.props.unload();
        this.props.load(state.collections,state.effects,state.sounds,state.app);
      }
    });
  }
  add(e){
    loadData(e,{
      onLoad: ()=>{

      },
      onFailure: ()=>{

      },
      onSuccess: (state) =>{
        const modalData = this.buildModalData(state);
        this.setState({showModal:true, modalData:modalData, addData: state});
      }
    }, this.props.state);
  }
  buildModalData(added){
    const collections = added.collections;
    const collectionIdArray = Object.keys(collections);
    const effects = added.effects;

    let modalData = [];

    collectionIdArray.forEach(function(collectionId){
      let collectionData = {id:collectionId,name:collections[collectionId].name};
      let collectionEffects = [];
      collections[collectionId].effects.forEach(function(effectId){
        collectionEffects.push({id:effectId,name:effects[effectId].name});
      });

      collectionData.effects = collectionEffects;
      modalData.push(collectionData);
    });

    return modalData;
  }

  export(){
    exportData(this.props.state);
    this.props.clearSaveReminderValue();
  }

  closeAddModal(){
    this.setState({showModal:false});
  }

  confirmAddModal(data){
    const that = this;
    this.setState({showModal:false});

    let collectionObj = Object.assign({},this.state.addData.collections);
    const collectionArr = Object.entries(collectionObj).splice(0);

    let effectObj = Object.assign({},this.state.addData.effects);
    const effectArr = Object.entries(effectObj);
    let effectRemoveArr = [];

    let soundObj = Object.assign({},this.state.addData.sounds);

    effectArr.forEach(function(e){
      if(!data[e[0]]){
        that.removeAddedEffect(e[0],effectObj, soundObj);
        effectRemoveArr.push(e[0]);
      }
    });

    collectionArr.forEach(function(c, ci){
      for(let ei = effectRemoveArr.length - 1; ei >= 0; ei--){
        const previousCount = collectionObj[c[0]].effects.length;
        console.log(previousCount);
        collectionObj[c[0]].effects = collectionObj[c[0]].effects.filter(id => id !== effectRemoveArr[ei]);
      }
      if(!data[c[0]]){
        that.removeAddedCollection(c[0], collectionObj, effectObj, soundObj);
      }
    });

    //this.props.unload();
    this.props.add(collectionObj,effectObj,soundObj);
  }

  removeAddedCollection(id, cObj, eObj, sObj){
    const that = this;
    const effects = cObj[id].effects;
    effects.forEach(function(e){
      that.removeAddedEffect(e, eObj, sObj)
    });
    delete cObj[id];
  }
  removeAddedEffect(id, eObj, sObj, data){
    const that = this;
    const sounds = eObj[id].sounds;
    sounds.forEach(function(s){
      that.removeAddedSound(s, sObj);
    });

    delete eObj[id];
  }
  removeAddedSound(id, sObj){
    delete sObj[id];
  }

  render(){
    return(
      <div className="sidebar sidebar-main dark ">
        <div className="sidebar-background"></div>
        {this.state.showModal &&
          <ModalBaseContainer title={"Add To Current Collection"} classes={"modal-add-collection"}>
            <AddCollectionModalContainer data={this.state.modalData} close={this.closeAddModal} confirm={(data)=>this.confirmAddModal(data)}/>
          </ModalBaseContainer>
        }
        <div className="sidebar-head">
          <h5>Sound Mixer</h5>
        </div>
        <div className="sidebar-content no-padding">
          <ul className="no-style">
            <li>
              <span>App</span>
            </li>
            <li>
              <button type="button" name="button" onClick={(e) => simulateClick(this.loadInput)}>
                <i className="fas fa-upload"></i>
                Load
              </button>
              <input type='file' style={{display:"none"}} onChange={this.load} ref={(ref) => {this.loadInput = ref}}></input>
            </li>
            <li>
              <button type="button" name="button" onClick={(e)=>simulateClick(this.addInput)}>
                <i className="fas fa-plus"></i>
                Add
              </button>
              <input type='file' style={{display:"none"}} onChange={this.add} ref={(ref) => {this.addInput = ref}}></input>
            </li>
            <li>
              <button type="button" name="button" onClick={this.export}>
                <i className="fas fa-download"></i>
                Export
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>{}} className="disabled">
                <i className="fas fa-box-open"></i>
                Soundscape Collection
              </button>
            </li>
            <li>
              <span>Soundscapes</span>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState("LIBRARY")}>
                <i className="fas fa-folder"></i>
                Sound Library
              </button>
            </li>
            <li>
              <span>Info</span>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState("HOWTO")}>
                <i className="fas fa-question"></i>
                How To Use
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState("ABOUT")}>
                <i className="fas fa-book-open"></i>
                About
              </button>
            </li>
            <li>
              <span>Donate</span>
            </li>
            <li>
            <button type="button" name="button" className="disabled">
              <i className="fab fa-patreon"></i>
              Patreon
            </button>
            </li>
            <li>
              <button type="button" name="button" className="disabled">
              <i className="fab fa-paypal"></i>
              Paypal
              </button>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    app: state.app,
    sounds: state.sounds,
    state: state
  };
}
const mapDispatchToProps = (dispatch) =>{
  return{
    unload: () =>{
      dispatch(unloadCollections());
      dispatch(unloadEffects());
      dispatch(unloadSounds());
    },
    add: (collections, effects, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(loadAdditionalSounds(sounds));
        dispatch(loadAdditionalEffects(effects));
        dispatch(loadAdditionalCollections(collections));
      });
    },
    load: (collections, effects, sounds, app) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
        dispatch(loadCollections(collections))
        dispatch(loadApp(app));
        dispatch(changeAppVar("loading",false));
      });
    },
    changeAppVar: (varName, value)=>{
      dispatch(changeAppVar(varName,value));
    }
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(ControlsContainer);



