import React, { Component } from 'react';
import {connect} from 'react-redux';

import CollectionContainer from './containers/CollectionContainer';
import ControlsContainer from './containers/ControlsContainer';
import SoundLibraryContainer from './containers/SoundLibraryContainer';
import AboutContainer from './containers/AboutContainer';
import HowToContainer from './containers/HowToContainer';
import HowToVideoContainer from './containers/HowToVideoContainer';

import ModalBaseContainer from './containers/ModalBaseContainer';
import SaveReminderModalContainer from './containers/SaveReminderModalContainer';
//import About from './components/about';
import {changeAppVar} from './actions/appActions';
import {isKeypressEnter} from './Helpers';

import {exportData} from './js/file_manager';

const sidebarStates = {MAIN: "MAIN", ABOUT: "ABOUT", LIBRARY: "LIBRARY", HOWTO: "HOWTO"}

class App extends Component {
  constructor(props){
    super(props);
    this.mediaButtonRef = this.mediaButtonRef.bind(this);
    this.changeSidebarState = this.changeSidebarState.bind(this);
    this.changeHowToVideoIndex = this.changeHowToVideoIndex.bind(this);
    this.changeName = this.changeName.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);

    this.saveReminderToggle = this.saveReminderToggle.bind(this);
    this.saveReminderCancel = this.saveReminderCancel.bind(this);
    this.saveReminderConfirm = this.saveReminderConfirm.bind(this);

    this.addSaveReminderValue = this.addSaveReminderValue.bind(this);
    this.checkSaveReminderValue = this.checkSaveReminderValue.bind(this);
    this.clearSaveReminderValue = this.clearSaveReminderValue.bind(this);
    this.clearSaveReminderTimer = this.clearSaveReminderTimer.bind(this);

    this.state = {
      mediaButtons: {},
      sidebarState: sidebarStates.MAIN,
      editingName:false,
      delayTimer:null,
      howToVideoIndex: 0,
      name: this.props.app.name,
      showSaveReminderModal: false,
      saveReminderValue: 0,
      saveReminderTimer: null
    }
  }
  componentWillUnmount(){
    this.clearSaveReminderTimer();
  }
  //Function for passing reference to effect media buttons up from children
  //In order to play sounds from sibling container
  mediaButtonRef(){
    //Function returns an object with 2 functions
    return {
      //Sets the reference to the play and pause buttons in the state
      setRef:(play, pause, id)=>{
        const ref = {[id]:{
          "play":play,
          "pause":pause
        }}
        this.setState(prevState =>({
          mediaButtons: Object.assign({}, prevState.mediaButtons,ref)
        }));
      },
      //Deletes the reference from the state on unload
      removeRef:(id)=>{
        this.setState(prevState =>({
          mediaButtons: delete prevState[id]
        }));
      }
    };
  }

  changeSidebarState(newVal){
    let newState = "";
    switch(newVal){
      case sidebarStates.LIBRARY:
        newState = sidebarStates.LIBRARY;
        break;
      case sidebarStates.ABOUT:
        newState = sidebarStates.ABOUT;
        break;
      case sidebarStates.HOWTO:
        newState = sidebarStates.HOWTO;
        break;
      default:
        newState = sidebarStates.MAIN;
    }

    this.setState({
      sidebarState : newState
    });
  }

  changeHowToVideoIndex(newIndex){
    this.setState({
      howToVideoIndex: newIndex
    });
  }

  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ""){
      this.props.changeAppVar("name",name);
    }
    this.setState({editingName:false})
  }
  nameInputChange(e){
    this.setState({name:e.target.value})
  }
  nameInputBlur(){
    const delayTimer = window.setTimeout(()=>{
      this.setState({editingName:false});
    },200);
    this.setState({delayTimer:delayTimer});
  }

  //Modal Stuff//

  saveReminderToggle(){
    this.setState({saveReminderModal:false});
  }

  saveReminderConfirm(){
    exportData(this.props.state);
    this.clearSaveReminderTimer();
    this.setState({showSaveReminderModal:false, saveReminderValue:0});
  }
  saveReminderCancel(){
    this.setState({showSaveReminderModal:false});
  }
  addSaveReminderValue(value){
    this.setState({saveReminderValue: this.state.saveReminderValue + value},()=>this.checkSaveReminderValue());
  }
  checkSaveReminderValue(){
    if(this.state.saveReminderTimer !== null){return;}
    if(this.state.saveReminderValue > 1000){
      const saveReminderTimer = setTimeout(()=>{
        this.setState({saveReminderTimer:null,showSaveReminderModal:true});
      },7000);
      this.setState({saveReminderTimer});
    }
  }
  clearSaveReminderValue(){
    this.clearSaveReminderTimer();
    this.setState({saveReminderValue:0});
  }
  clearSaveReminderTimer(){
    const {saveReminderTimer} = this.state;
    if(saveReminderTimer !== null){
        clearTimeout(saveReminderTimer);
    }
  }

  render() {

    const name = () =>{
      if(this.state.editingName){
        return(
          <div className="app-name-input">
            <input type="text" onKeyPress={(e)=>isKeypressEnter(e,()=>this.changeName())} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} value={this.state.name} onBlur={this.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return(<h5 onClick={()=>this.setState({editingName:true})}>{this.props.app.name}</h5>);
    }

    const sidebar = () =>{
      return (
        <div>
          <ControlsContainer mediaButtons={this.state.mediaButtons} changeAppVar={this.props.changeAppVar} changeSidebarState={(v) => this.changeSidebarState(v)} clearSaveReminderValue={this.clearSaveReminderValue}/>
          <SoundLibraryContainer classes={this.state.sidebarState !== sidebarStates.LIBRARY ? "hidden" : ""} changeSidebarState={(v)=>this.changeSidebarState(v)}/>
          <AboutContainer classes={this.state.sidebarState !== sidebarStates.ABOUT ? "hidden":""} changeSidebarState={(v)=>this.changeSidebarState(v)}/>
          <HowToContainer videoIndex={this.state.howToVideoIndex} classes={this.state.sidebarState !== sidebarStates.HOWTO ? "hidden" : ""} changeVideoIndex={(i)=>this.changeHowToVideoIndex(i)} changeSidebarState={(v)=>this.changeSidebarState(v)}/>
        </div>
      );
    }

    return (
      <div className="container-fluid no-gutters">
        {this.state.showSaveReminderModal &&
          <ModalBaseContainer title="Would You Like To Save?" classes="modal-save-reminder">
            <SaveReminderModalContainer confirm={this.saveReminderConfirm} close={this.saveReminderCancel}/>
          </ModalBaseContainer>
        }
        <div className="row no-gutters">
          <div className="col-12 col-md-3 col-lg-3 col-xl-2">
            <div className="sidebar-container">
              {sidebar()}
            </div>
          </div>
          <div className="col-12 col-md-9 col-lg-9 col-xl-10">
            {(()=>{
              if(this.state.sidebarState === sidebarStates.HOWTO){
                return (<HowToVideoContainer videoIndex={this.state.howToVideoIndex}/>)
              }
            })()}
            <div className={"soundscape-builder " + (this.state.sidebarState === sidebarStates.HOWTO ? "hidden":"")}>
              <div className="builder-head">
                {name()}
              </div>
              <CollectionContainer addSaveReminderValue={(value)=>this.addSaveReminderValue(value)}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
// <DragDropContext onDragEnd={this.onDragEnd}>
//   <EffectContainer mediaButtonRef={this.mediaButtonRef}/>
// </DragDropContext>

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    app: state.app,
    state: state
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    changeAppVar:(varName,value)=>{
      dispatch(changeAppVar(varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



