import React, {Component} from 'react';
import AddCollectionModal from '../components/addCollectionModal';

class AddCollectionModalContainer extends Component{
  constructor(props){
    super(props);
    this.buildCheckedItemsState = this.buildCheckedItemsState.bind(this);
    this.toggleChecked = this.toggleChecked.bind(this);
    this.state = {
      checkedItems: {}
    }
  }

  componentDidMount(){
    this.buildCheckedItemsState(this.props.data);
  }

  buildCheckedItemsState(data){
    let items = {};
    data.forEach(function(i){
      items[i.id] = true;
      i.effects.forEach(function(i){
        items[i.id] = true;
      });
    });
    this.setState({checkedItems:items});
  }

  toggleChecked(id){
    if(typeof this.state.checkedItems[id] === 'undefined'){return;}

    let items = Object.assign({},this.state.checkedItems);
    items[id] = !items[id];

    this.setState({checkedItems:items});
  }

  render(){
    return(
      <AddCollectionModal data={this.props.data} toggleChecked={this.toggleChecked} checkedItems={this.state.checkedItems} close={this.props.close} confirm={()=>this.props.confirm(this.state.checkedItems)}/>
    )
  }
}

export default AddCollectionModalContainer;



